body {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    background-color: #221e45;
    font-size: 16px;
    color: #fff;
}
.movies {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    border-left: 1px solid #313131;
    border-right: 1px solid #313131;
}
.movie {
    border-radius: 10px;
    -webkit-box-shadow: 0px 0px 19px 2px rgba(0, 0, 0, 0.2);
    -moz-box-shadow: 0px 0px 19px 2px rgba(0, 0, 0, 0.2);
    box-shadow: 0px 0px 19px 2px rgba(0, 0, 0, 0.2);
    position: relative;
    background-color: #191936;
    width: 200px;
    height: 300px;
    display: flex;
    flex-direction: column;
    margin: 40px 10px;
}
.desc {
    display: flex;
    flex-direction: column;
    position: absolute;
    bottom: -32px;
    left: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.73);
}
.ov {
    display: flex;
    padding: 7px;
    justify-content: space-between;
}
.bDes {
    width: 100%;
    background-color: #24244e;
    border-radius: 4px;
    color: #fff;
}
.green {
    color: green;
}
.red {
    color: red;
}
.yellow {
    color: yellow;
}
img {
    object-fit: cover;
    width: 100%;
    height: auto;
}
.seemore {
    -webkit-box-shadow: 0px 0px 19px 2px rgba(0, 0, 0, 0.2);
    -moz-box-shadow: 0px 0px 19px 2px rgba(0, 0, 0, 0.2);
    box-shadow: 0px 0px 19px 2px rgba(0, 0, 0, 0.2);
    background-color: #252452;
    color: white;
    position: absolute;
    bottom: 20px;
    left: 50%;
    transform: translateX(-50%);
}
.container {
    padding-bottom: 100px;
    position: relative;
}
.overlay {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.47);
}
.wrapper {
    position: fixed;
    text-align: center;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    width: 300px;
    height: 150px;
    border-radius: 10px;
    background-color: #221e45;
    padding: 20px;
}
.modalW {
    display: none;
}
.open {
    display: block;
}